.footer {
  background: linear-gradient(45deg, #ff930f 0%, #f9f9f6 50%);
  border-top: 1px solid #fff;
}

.footer__container {
  padding: 2rem 0 3rem;
}
.footer__title .footer__link {
  color: var(--title-color);
}

.footer__title {
  text-align: center;
  color: var(--title-color-green);
}
.footer__title img {
  height: 11rem;
  width: 11rem;
}

.footer__link:hover {
  color: var(--title-color-green);
}

.footer__list {
  display: flex;
  justify-content: center;
  column-gap: 1.5rem;
  margin-bottom: var(--mb-1);
}

.footer__social {
  display: flex;
  justify-content: center;
  column-gap: 1.25rem;
}

.footer__social-link {
  font-size: 1.5rem;
  color: var(--title-color);
}
.footer__social-link:hover {
  color: var(--title-color-green);
}

.footer__copy {
  display: block;
  margin-top: 1.5rem;
  color: var(--title-color);
  text-align: center;
  font-size: var(--smaller-font-size);
}

.footer__link {
  color: var(--title-color);
}
